.top-nav {
  background-color: #333;
  padding: 10px 0;
}

.top-nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.top-nav li {
  margin: 0 15px;
}

.top-nav a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  transition: color 0.3s ease;
}

.top-nav a:hover {
  color: #61dafb;
}
